












































































































@import "@design";
$spacing: 4rem;

.fade-leave-active {
  transition: opacity 1s;
}
.fade-enter-active {
  transition: opacity 2s;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.container {
  padding: $spacing;
  height: 100vh;
  width: 100vw;
  box-sizing: border-box;
}
.background {
  position: absolute;
  top: 0;
  left: 0;
  &__image {
    height: 100vh;
    width: 100vw;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%, 50%;
    position: fixed;
  }
}
.logo {
  border: 2px solid white;
  padding: 1rem;
  width: 8rem;
  height: 8rem;
  display: flex;
  align-items: center;
  text-align: center;
  background: rgba(255, 255, 255, 0.3);
  font-weight: bold;
  letter-spacing: 0.25rem;
  margin: 0;
  position: relative;
  &__container {
    position: absolute;
    z-index: 1;
  }
  &__link {
    &:before {
      position: absolute;
      content: "";
      background-size: auto auto;
      background-color: rgba(255, 255, 255, 0.3);
      background-image: repeating-linear-gradient(
        45deg,
        transparent,
        transparent 3px,
        white 3px,
        white 4px
      );
      z-index: -1;
      top: -10px;
      left: -10px;
      width: 100%;
      height: 100%;

      clip-path: polygon(
        0% 0%,
        0% 100%,
        10px 100%,
        10px 10px,
        100% 10px,
        100% 100%,
        100% 0%
      );
    }
  }
}
.aside {
  position: absolute;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: calc(100% - #{$spacing * 2});
}
.active-room {
  position: absolute;
  bottom: $spacing;
  font-size: 1.25rem;
  &__number {
    font-size: 4rem;
    display: inline-block;
    position: relative;
    &:before {
      content: "";
      background-size: auto auto;
      background-color: rgba(255, 255, 255, 0.3);
      background-image: repeating-linear-gradient(
        45deg,
        transparent,
        transparent 3px,
        rgba(255, 255, 255, 1) 3px,
        rgba(255, 255, 255, 1) 4px
      );
      position: absolute;
      bottom: 15px;
      height: 15px;
      width: 100%;
    }
  }
}
.room {
  position: relative;
  padding: 1rem 3rem 1rem 2rem;
  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    width: 0;
    height: 1px;
    background: $color-text-primary;
    top: 50%;
    transition: $all-transition;
  }
  &__list {
    background: white;
    padding: 2rem 0;
    position: relative;
    &::before {
      content: "";
      background-size: auto auto;
      background-color: rgba(255, 255, 255, 0.3);
      background-image: repeating-linear-gradient(
        45deg,
        transparent,
        transparent 3px,
        white 3px,
        white 4px
      );
      width: calc(100% - 15px);
      left: 15px;
      height: 100%;
      position: absolute;
      top: 15px;
    }
  }
  &__link {
    display: block;
    color: initial;
    &:hover,
    &--active {
      color: lighten($color-text-primary, 20);
      .room::before {
        width: 2rem - 0.5rem;
      }
    }
  }
}
.information {
  padding: 0 3rem 0 2rem;
  &__icon {
    margin-right: 10px;
  }
}
.social {
  display: inline-flex;
  margin-bottom: 10px;
}
